.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
  }
  
  .total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
  }

  .total .total-amount{
    border: 1px solid rgb(68, 64, 64);
    border-radius: 10px;
    padding: 0.3rem 0.7rem;
    color: rgb(39, 38, 38)
  }
  
  .actions {
    text-align: right;
  }
  
  .actions button {
    font: inherit;
    cursor: pointer;
    padding: 0.5rem 2rem;
    border-radius: 10px;
    margin-left: 1rem;
  }

  .fields {
    margin-top: 1rem; 
  }
  
  